*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu Mono", monospace;
  display: grid;
  width: 100vw;
  height: 100vh;
  background-color: black;
  min-width: 320px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.close {
  display: none;
}

.open {
  position: relative;
  width: 90vw;
  height: fit-content;
  background-color: rgb(4, 0, 122);
  outline: solid 5px rgb(0, 204, 255);
  margin: auto;
  padding: 5vh 20px;
  min-width: fit-content;
}

.modal__buttons-wrapper {
  display: flex;
}

.modal__button {
  cursor: pointer;
  font-family: inherit;
  font-weight: 700;
  font-size: calc(16px + 1.25vw);
  text-transform: uppercase;
  color: white;
  width: 30vw;
  height: 10vh;
  display: block;
  margin: 0 auto;
  border: 2px solid white;
  border-radius: 5px;
}
.modal__button--start {
  background-color: rgb(122, 201, 4);
}
.modal__button--info {
  background-color: orange;
}
.modal__button:hover {
  opacity: 0.9;
}
.modal__button:active {
  transform: scale(0.8);
}
@media (max-width: 510px) {
  .modal__button {
    font-size: calc(16px + 1.25vw);
    width: 26vw;
    height: 10vh;
  }
}

.game__color-word {
  display: block;
  font-size: 1.5em;
}
.game__color-word:hover {
  animation-name: rotate-word;
  animation-duration: 2s;
}

@keyframes rotate-word {
  from {
    transform: rotateY(0);
  }
  50% {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0);
  }
}
.game__pause {
  position: absolute;
  right: 0;
  top: 0;
  background-image: url(../img/pause.png);
}

.green {
  background-color: green;
}

.red {
  background-color: red;
}

.yellow {
  background-color: yellow;
}

.pink {
  background-color: hotpink;
}

.blue {
  background-color: rgb(0, 195, 255);
}

.orange {
  background-color: orange;
}

.violet {
  background-color: rgb(155, 11, 187);
}

.beige {
  background-color: rgb(230, 218, 166);
}

.brown {
  background-color: rgb(150, 75, 0);
}

.green-word {
  color: green;
}

.red-word {
  color: red;
}

.yellow-word {
  color: yellow;
}

.pink-word {
  color: hotpink;
}

.blue-word {
  color: rgb(0, 195, 255);
}

.orange-word {
  color: orange;
}

.violet-word {
  color: rgb(155, 11, 187);
}

.beige-word {
  color: rgb(230, 218, 166);
}

.brown-word {
  color: rgb(150, 75, 0);
}

.instruction {
  font-size: calc(32px + 1.25vw);
  font-weight: 700;
  color: white;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 5vh;
}
@media (max-width: 768px) {
  .instruction {
    font-size: calc(24px + 1.25vw);
  }
}
@media (max-width: 510px) {
  .instruction {
    font-size: calc(24px + 1.25vw);
  }
}

.button {
  cursor: pointer;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: 50px 50px;
  background-color: transparent;
  background-position: center;
  border: none;
}
@media (max-width: 780px) {
  .button {
    width: 35px;
    height: 35px;
    background-size: 30px 30px;
  }
}

.dialog {
  background-color: rgb(4, 0, 122);
  border: 10px solid white;
  width: 90vw;
}
.dialog::backdrop {
  background-color: black;
}

.dialog__wrapper {
  display: grid;
}

.dialog__img {
  width: 100%;
  height: auto;
}

.dialog__description {
  color: #fff;
  line-height: 1.5;
  font-size: calc(16px + 1.25vw);
}

.dialog__close {
  position: absolute;
  right: 0;
  top: 0;
  background-image: url(../img/close.png);
}

.dialog__statistics {
  font-size: calc(18px + 1.25vw);
  color: white;
  display: grid;
  grid-template-columns: 2fr 1fr;
  white-space: nowrap;
}
@media (max-width: 510px) {
  .dialog__statistics {
    font-size: calc(14px + 1.25vw);
  }
}
@media (max-width: 360px) {
  .dialog__statistics {
    font-size: calc(10px + 1.25vw);
  }
}

.dialog__key {
  padding-right: 10px;
}

.dialog__key:nth-of-type(even),
.dialog__value:nth-of-type(even) {
  border-top: 2px solid white;
  border-bottom: 2px solid white;
}

.dialog__value {
  width: 50%;
  margin: 0;
  padding-left: 5%;
  border-left: 2px solid white;
}

.dialog__color-word {
  display: block;
  font-size: 1.5em;
}

.dialog__buttons-wrapper {
  margin-bottom: 30px;
}

.dialog__note {
  display: none;
  position: absolute;
  width: 60px;
  height: 60px;
}
.dialog__note--correct {
  display: block;
  background-repeat: no-repeat;
  background-size: 50px 50px;
  background-color: transparent;
  background-position: center;
  border: none;
  background-image: url(../img/correct.png);
  animation-name: show-correct;
  animation-duration: 2s;
}
.dialog__note--falsy {
  display: block;
  color: white;
  width: 100px;
  height: fit-content;
}

@keyframes show-correct {
  from {
    transform: rotate(-90deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(-45deg);
  }
  75% {
    transform: rotate(45deg);
  }
  to {
    transform: rotate(0);
  }
}
.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.title {
  margin: 0;
}

.play-button {
  cursor: pointer;
  width: 10vw;
  height: 5vh;
  border: 3px solid black;
  box-shadow: -5px 4px 0 0 black;
  border-radius: 5px;
}
.play-button--correct:hover {
  transform: scale(1.5);
}